.section {
  background-color: rgb(215, 228, 240);
  height: 100vh;
  padding-top: 5px;
  padding-left: 30px;
}
.title {
  font-size: 32px;
  font-weight: bold;
  color: #095d96;
  padding: 10px;
}
.list {
  /* list-style-type: none; */
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.item {
  font-size: 25px;
  color: #4a4b9c;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}
.item:hover {
  color: #c7670d;
}
